<template>
  <v-ons-page>
    <div class="content_area">
      <v-ons-list>
        <v-ons-list-header>
          빌린 물건
        </v-ons-list-header>
        <item-row v-for="item in itemList" :Item="item" v-bind:key="item"></item-row>
      </v-ons-list>
    </div>

    <v-ons-modal :visible="loading">
      <p style="text-align: center">
        Loading <v-ons-icon icon="fa-spinner" spin></v-ons-icon>
      </p>
    </v-ons-modal>
  </v-ons-page>
</template>

<script>

import ItemRow from './ItemRow.vue';

export default {
  key: "MyPage",
  name: "MyPage",
  toolBarName: "BeBlet",
  data: function() {
    return {
      loading: false,
      get id() {
        return window.localStorage.getItem("id");
      }
    }
  },
  components: {
    ItemRow
  },
  computed: {
    itemList() {
      return this.$store.state.itemList;
    }
  }
}
</script>

<style>

</style>


